html,
body {
  margin: 0;
  padding: 0;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.carousel-container {
  height: 480px;
  margin-top: 90px;
  background: white;
}
.slide {
  height: 480px;
  background-size: cover;
  background-repeat: no-repeat;
}
.slide1 {
  background-image: url(/final-project-frontend/static/media/slide1.dbc89779.jpg);
}
.slide2 {
  background-image: url(/final-project-frontend/static/media/slide2.1342c85f.jpg);
}
.slide3 {
  background-image: url(/final-project-frontend/static/media/slide3.79526cba.jpg);
}
.slide4 {
  background-image: url(/final-project-frontend/static/media/slide4.2eb16a64.jpg);
}
.carousel-container li {
  background-color: grey;
  border: none;
  height: 10px;
  width: 30px;
}
.carousel-container li button {
  border: none;
  color: white;
  text-align: center;
  margin: 4px 2px;
  cursor: pointer;
}
.main-text {
  color: white;
}
.sub-text {
  color: azure;
}
.button-container-carousel {
  padding: 10px;
  margin: 10px 10px 0 10px;
  background: rgb(223, 105, 223);
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

