.account-main-container {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  background-image: -webkit-gradient(
    linear,
    left bottom, right top,
    from(#8a108a),
    color-stop(#9b299b),
    color-stop(#ad3dad),
    color-stop(#bf4fbf),
    color-stop(#d161d1),
    color-stop(#da64da),
    color-stop(#e366e3),
    color-stop(#ec69ec),
    color-stop(#ed5ced),
    color-stop(#ef4def),
    color-stop(#f03bf0),
    to(#f11ff1)
  );
  background-image: -webkit-linear-gradient(
    left bottom,
    #8a108a,
    #9b299b,
    #ad3dad,
    #bf4fbf,
    #d161d1,
    #da64da,
    #e366e3,
    #ec69ec,
    #ed5ced,
    #ef4def,
    #f03bf0,
    #f11ff1
  );
  background-image: linear-gradient(
    to right top,
    #8a108a,
    #9b299b,
    #ad3dad,
    #bf4fbf,
    #d161d1,
    #da64da,
    #e366e3,
    #ec69ec,
    #ed5ced,
    #ef4def,
    #f03bf0,
    #f11ff1
  );
  padding-top: 5rem;
  padding-bottom: 5rem;
}

.account-main-container .container {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.account-main-container .card {
  background-color: rgba(51, 51, 77, 0.05);
  border-width: 0.2rem;
}

#dashboard-jumbotron-container {
  margin-bottom: 0;
  background: url("https://bogatiurns.com/wp-content/uploads/2017/05/pets.jpg")
    fixed top no-repeat;
  background-size: cover;
  color: rgba(0, 0, 0, 0.8);
  height: 27rem;
  position: relative;
  z-index: -2;
}

#dashboard-jumbotron-container .overlay {
  background-color: #000;
  opacity: 0.2;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: -1;
}

.user-dashboard-container {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  background-image: -webkit-gradient(
    linear,
    left bottom, right top,
    from(#8a108a),
    color-stop(#9b299b),
    color-stop(#ad3dad),
    color-stop(#bf4fbf),
    color-stop(#d161d1),
    color-stop(#da64da),
    color-stop(#e366e3),
    color-stop(#ec69ec),
    color-stop(#ed5ced),
    color-stop(#ef4def),
    color-stop(#f03bf0),
    to(#f11ff1)
  );
  background-image: -webkit-linear-gradient(
    left bottom,
    #8a108a,
    #9b299b,
    #ad3dad,
    #bf4fbf,
    #d161d1,
    #da64da,
    #e366e3,
    #ec69ec,
    #ed5ced,
    #ef4def,
    #f03bf0,
    #f11ff1
  );
  background-image: linear-gradient(
    to right top,
    #8a108a,
    #9b299b,
    #ad3dad,
    #bf4fbf,
    #d161d1,
    #da64da,
    #e366e3,
    #ec69ec,
    #ed5ced,
    #ef4def,
    #f03bf0,
    #f11ff1
  );
  padding-bottom: 5rem;
}

.user-dashboard-container .container {
  text-align: center;
}

.user-dashboard-container .card {
  background-color: rgba(51, 51, 77, 0.05);
  border-width: 0.2rem;
}

.user-dashboard-container .card-body {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.account-details,
.user-favorites,
.user-submitted-forms {
  text-align: left;
}

.user-favorites .card {
  justify-content: center;
  align-items: center;
}

.user-favorite-card .card {
  background: none;
  border: none;
  padding: 0.5rem;
  min-width: 10rem;
  max-width: 15rem;
  min-height: 5rem;
  max-height: 15rem;
  margin: auto;
}

.user-favorite-card .card-body {
  padding: 0;
  position: relative;
}

.user-favorite-card .overlay {
  position: absolute;
  bottom: 0;
  background: rgb(0, 0, 0);
  background: rgba(0, 0, 0, 0.5);
  color: #f1f1f1;
  width: 100%;
  -webkit-transition: 0.5s ease;
  transition: 0.5s ease;
  opacity: 0;
  color: white;
  font-size: 1rem;
  padding: 1rem;
  text-align: center;
}

.user-favorite-card .card-body:hover .overlay {
  opacity: 1;
}

.user-submitted-card .card {
  justify-content: center;
  align-items: center;
}

.user-submitted-card .card {
  background: none;
  border: none;
  padding: 0.5rem;
  margin: auto;
}

.user-submitted-card .card-body {
  border-width: 1px;
  border-style: solid;
  border-color: black;
}

